@import url(nullstyles.css);
@import url(common.css);


body {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow: auto;
}

.container {
  height: 100%;
  max-width: 1920px;
  max-height: 1080px;
  margin: 0 auto;
}

.content {
  display: flex;
  height: 100%;
  width: 100%;
  min-width: 1180px;
  min-height: 600px;
}

.sidenav {
  display: flex;
  flex: 1 0 auto;
  min-width: 160px;
  max-width: 12%;
  min-height: 100%;
  flex-direction: column;
  cursor: pointer;
  background: #eeeeee;
}
.sidenav .sidenav__logo {
  display: block;
  background-color: rgba(196, 196, 196, 0.4117647059);
  margin: 0 auto;
  width: 100%;
  text-align: center;
  padding: 10px;
  border-radius: 15px;
  height: 44px;
}
.sidenav .sidenav__logo .sidenav__title {
  display: inline;
  color: #565656;
  font-size: 20px;
  font-weight: 800;
  line-height: 24px;
  letter-spacing: 1px;
}
.sidenav .sidenav__logo:hover .sidenav__title {
  color: rgba(86, 86, 86, 0.6235294118);
}
.sidenav .sidenav__link {
  padding: 5px 0 0 0;
}
.sidenav .sidenav__link .sidenav__item {
  background-color: rgba(196, 196, 196, 0.2274509804);
  border-radius: 15px;
}
.sidenav .sidenav__link .sidenav__item .first-level {
  padding: 10px 15px;
  margin: 5px 0;
}
.sidenav .sidenav__link .sidenav__item .first-level .first-level__text {
  margin: 0px 30px 0 0;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 1px;
  color: #565656;
  font-weight: 600;
}
.sidenav .sidenav__link .sidenav__item .first-level.first-level_arrow {
  position: relative;
}
.sidenav .sidenav__link .sidenav__item .first-level.first-level_arrow::after, .sidenav .sidenav__link .sidenav__item .first-level.first-level_arrow::before {
  content: "";
  width: 11px;
  height: 1px;
  background-color: #565656;
  position: absolute;
  top: 50%;
  transition: all 0.3s ease 0s;
}
.sidenav .sidenav__link .sidenav__item .first-level.first-level_arrow::before {
  transform: rotate(40deg);
  right: 18px;
}
.sidenav .sidenav__link .sidenav__item .first-level.first-level_arrow::after {
  transform: rotate(-40deg);
  right: 10px;
}
.sidenav .sidenav__link .sidenav__item .first-level.first-level_arrow.active::after {
  transform: rotate(40deg);
}
.sidenav .sidenav__link .sidenav__item .first-level.first-level_arrow.active::before {
  transform: rotate(-40deg);
}
.sidenav .sidenav__link .sidenav__item:hover {
  background-color: rgba(196, 196, 196, 0.5176470588);
  border-radius: 15px;
}
.sidenav .sidenav__link .sidenav__item.active {
  background-color: rgba(39, 4, 238, 0.2196078431);
  border-radius: 15px;
}
.sidenav .sidenav__link .sidenav__item .second-level {
  display: none;
}
.sidenav .sidenav__link .sidenav__item .second-level__block {
  display: flex;
  flex-direction: column;
  border-radius: 15px;
}
.sidenav .sidenav__link .sidenav__item .second-level__block .second-level__text {
  display: inline;
  font-size: 13px;
  line-height: 20px;
  letter-spacing: 1px;
  color: #565656;
  font-weight: 600;
  padding: 5px 10px 5px 30px;
}
.sidenav .sidenav__link .sidenav__item .second-level__block .second-level__text:last-child {
  padding: 3px 10px 10px 30px;
}
.sidenav .sidenav__link .sidenav__item .second-level__block .second-level__text:first-child {
  padding: 0 10px 0 30px;
}
.sidenav .sidenav__link .sidenav__item .second-level__block .second-level__text:hover {
  color: #1f1f1f;
}

.main {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  flex: 1 1 auto;
}

.navbar {
  background-color: #eeeeee;
  width: 100%;
  flex: 0 0 auto;
}
.navbar__row {
  height: 44px;
  background-color: rgba(196, 196, 196, 0.4117647059);
  border-radius: 15px;
  display: flex;
  justify-content: space-between;
  margin-left: 10px;
}
.navbar__user {
  display: flex;
  margin: 8px 10px;
}
.navbar__user-info {
  display: flex;
  flex: 1 1 auto;
  color: #565656;
  font-weight: 600;
  line-height: 24px;
}
.navbar__user-info .user-name {
  margin: 0 10px;
}
.navbar__user-info .user-role {
  margin: 0 10px;
}
.main-body {
  flex: 1 1 auto;
  background-color: #eeeeee;
  overflow: hidden;
  padding: 10px;
}