.spoiler-num-cycle {
  counter-reset: num;
  padding: 10px;
}
.spoiler-num-cycle__block {
  display: none;
  margin: 10px 10px 0 40px;
}
.spoiler-num-cycle__item {
  display: block;
  margin: 20px 0 15px 0px;
  position: relative;
}
.spoiler-num-cycle__item:first-child {
  margin: 10px 0 10px 0px;
}
.spoiler-num-cycle__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 15px;
  border: 2px solid #b1b1b1;
  z-index: 0;
}
.spoiler-num-cycle__item:not(:last-child)::before {
  content: "";
  bottom: -20px;
}
.spoiler-num-cycle__item:last-child::before {
  content: "";
  bottom: 0;
}
.spoiler-num-cycle__item_completed::before {
  border: 2px solid #37b700;
}
.spoiler-num-cycle__item_completed .spoiler-num-cycle__title::before {
  background: #37b700;
  border: 1px solid #37b700;
}
.spoiler-num-cycle__item_progress::before {
  border: 2px solid #f7bb4b;
}
.spoiler-num-cycle__item_progress .spoiler-num-cycle__title::before {
  background: #f7bb4b;
  border: 1px solid #f7bb4b;
}
.spoiler-num-cycle__title {
  font-size: 12px;
  text-transform: uppercase;
  text-decoration: underline;
  font-weight: 700;
  letter-spacing: 1px;
  cursor: pointer;
  padding: 0 0 0 40px;
  position: relative;
}
.spoiler-num-cycle__title::before {
  content: counter(num);
  counter-increment: num;
  display: inline-block;
  position: absolute;
  top: -5px;
  left: 5px;
  width: 24px;
  height: 24px;
  line-height: 24px;
  color: #ffffff;
  background: #b1b1b1;
  border: 1px solid #b1b1b1;
  text-align: center;
  font-size: 16px;
  border-radius: 50%;
  cursor: pointer;
  z-index: 1;
  font-weight: normal;
}
.spoiler-num-cycle__text {
  padding: 10px;
}

.spoiler-arrow {
  counter-reset: num;
}
.spoiler-arrow__block {
  display: none;
  border-radius: 10px;
  margin: 10px 10px 0 10px;
  padding: 5px;
  border: 1px solid #bebebe;
  box-shadow: 0 0 15px 3px #c7c7c7;
}
.spoiler-arrow__block_no-border {
  border: none;
  padding: 0;
}
.spoiler-arrow__block-content {
  display: flex;
}
.spoiler-arrow__item {
  display: block;
  margin: 20px 0 15px 0px;
  position: relative;
}
.spoiler-arrow__item:first-child {
  margin: 10px 0 10px 0px;
}
.spoiler-arrow__title {
  font-size: 14px;
  text-transform: uppercase;
  text-decoration: underline;
  font-weight: 700;
  letter-spacing: 1px;
  cursor: pointer;
  padding: 0 0 0 40px;
  position: relative;
}
.spoiler-arrow__title::after, .spoiler-arrow__title::before {
  content: "";
  width: 11px;
  height: 2px;
  background-color: #565656;
  position: absolute;
  top: 50%;
  transition: all 0.3s ease 0s;
}
.spoiler-arrow__title::before {
  transform: rotate(40deg);
  left: 7px;
}
.spoiler-arrow__title::after {
  transform: rotate(-40deg);
  left: 15px;
}
.spoiler-arrow__title.active::after {
  transform: rotate(40deg);
  background-color: red;
}
.spoiler-arrow__title.active::before {
  transform: rotate(-40deg);
  background-color: red;
}
.spoiler-arrow__text {
  padding: 10px;
}

