  flex: 0 0 35%;
  display: flex;
  flex-direction: column;
  padding: 5px;
  position: relative;
}
.card-effects__title {
  font-size: 14px;
  text-align: center;
}
.card-effects__content {
  margin: 5px 0 0 0;
  padding: 0 5px;
  flex: 1 1 auto;
}
.card-effects__plastic-block {
  display: flex;
  justify-content: space-between;
}
.card-effects .additional-card-effects-block {
  min-height: 130px;
  max-height: 130px;
  overflow-y: scroll;
  font-size: 12px;
  padding: 0 10px;
}

.card-main-effects {
  flex: 0 0 25%;
  display: flex;
  flex-direction: column;
  padding: 5px;
  position: relative;
}
.card-main-effects::before {
  position: absolute;
  content: "";
  height: 95%;
  width: 1px;
  background-color: #b7b4b4;
  top: 2.5%;
  left: 100%;
}
.card-main-effects__title {
  font-size: 14px;
  text-align: center;
}
.card-main-effects__content {
  margin: 5px 0 0 0;
  padding: 0 5px;
  flex: 1 1 auto;
}

.card-additional-effects {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  padding: 5px;
  position: relative;
}
.card-additional-effects__title {
  font-size: 14px;
  text-align: center;
}
.card-additional-effects__content {
  margin: 5px 0 0 0;
  padding: 0 5px;
  flex: 0 0 auto;
  max-height: 90%;
}
.card-additional-effects__plastic-block {
  display: flex;
  justify-content: space-between;
}
.card-additional-effects .additional-card-effects-block {
  overflow-y: scroll;
  font-size: 12px;
  padding: 0 10px;
  margin: 0 5px 0 0;
  height: 300px;
}